.card {
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
    transition: 0.3s;
    width: 260px;
    border-radius: 5px;
  }
  .row {margin: 0 10px;}
  .column {
    float: left;
    padding: 0 10px;
  }  
  .card:hover {
    box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2);
  }
  img {
    border-radius: 5px 5px 0 0;
  }
  